import React, { useState, useEffect } from "react";

function Restrict() {
  const restrictMessages = [
    "Focus: your dreams are waiting for you to catch up.",
  "Distractions don't build futures, dedication does.",
  "Eyes on the goal, not on the temptation.",
  "Pause the scroll, unleash your potential.",
  "Not now, distractions. I'm building my empire.",
  "Every minute distracted is a step away from success.",
  "Conquer your distractions, unleash your focus.",
  "Distractions delay dreams. Stay focused.",
  "Remember why you started. Keep going.",
  "Distraction is the thief of vision. Guard your time.",
  "Focus is the weapon against the chaos of distraction."
  ];

  const [randomMessage, setRandomMessage] = useState("");

  useEffect(() => {
    const randomIndex = Math.floor(Math.random() * restrictMessages.length);
    setRandomMessage(restrictMessages[randomIndex]);
  }, []);

  return (
    <div
      style={{
        position: "absolute",
        top: 0,
        left: 0,
        zIndex: 99999,
        width: "100%",
        height: "100vh",
        background: "white",
      }}
    >
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <img
          src="https://cdn.dribbble.com/users/285475/screenshots/2083086/dribbble_1.gif"
          alt="restrict gif"
        />
      </div>

      <h3 style={{ textAlign: "center", fontSize: "30px", color: "black" }}>
        {randomMessage}
      </h3>
    </div>
  );
}

export default Restrict;
